* {
  font-family: Helvetica, Arial, sans-serif;
  background-color: #e0e0e0;
}

body {
  font-size: 18px;
  padding: 60px;
}

input {
  width: 100%;
  font-size: 1.3rem;
  border: 1px solid #000;
  border-radius: 10px;
  color: gray;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 8px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}
